.message {
    display: flex;
    justify-content: flex-start;
}
.message img {
    height: 70px;
    width:70px;
    border-radius: 65px;
    margin-top: 100px;
}
.box {
    width: 300px;
    margin: 40px;
    border-radius: 15px;
    background:mediumpurple;
    color: #282c34;
    padding: 20px;
    text-align:left;
    font-weight: 800;
    font-family: arial;
    position: relative;
}

.triangle:before {
    content: "";
    width: 0px;
    height: 0px;
    position: absolute;
    border-left: 15px solid transparent;
    border-right: 15px solid mediumpurple;
    border-top: 15px solid transparent;
    border-bottom: 15px solid mediumpurple;
    left: -16px;
    top: 76px;
}
.bigBox {
    width: 400px;
    margin: 20px;
    /*border-radius: 15px;*/
    background:cyan;
    /*color: darkcyan;*/
    padding: 10px;
    text-align:left;
    /*font-weight: 300;*/
    font-family: arial;
    position: relative;
}